	module.exports = {"data":{"post":{"html":"<p>Mama and I have to coordinate our schedules pretty closely. Actually, I need to coordinate to her schedule, since my schedule doesn't change much. I knew her boss was in town Tuesday and Wednesday this week, but Mama could still wake Christopher on Wednesday. And she could pick him up after work on Thursday, today.</p>\n<p>So this morning when I woke up, I was somewhat surprised to find her alarm turned off and that she was already up. And dressed. And gone. </p>\n<p>Good thing I noticed she was gone, since often during the week we don't see each other in the morning. Otherwise I would have driven off, and Christopher would have played trains all day long alone at home.</p>\n<p>Turns out she had an early meeting this morning, but could come home early this afternoon. She was so happy that she could cover for me in the afternoon that she forgot to say that I had to take her shift in the morning. I was a bit angry at first, but since I'm the flexible one, and not the breadwinner one, I grudgingly woke Christopher, brought him to his <em>Tagesmutter</em>, got stuck in traffic on the way to the city, and got to work 2 hours later than I had planned.</p>","fields":{"title":"Who's On First?","date":"31 Oct 2002","url":"https://papascott.de/2002-10-31-whos-on-first/"}}},"pathContext":{"slug":"/2002-10-31-whos-on-first/","prev":{"fields":{"title":"Sweet or Sour!","date":"31 Oct 2002","slug":"/2002-10-31-sweet-or-sour/"}},"next":{"fields":{"title":"One Of Us Now","date":"02 Nov 2002","slug":"/2002-11-02-one-of-us-now/"}}}}

